<template><section><h1>SuperMap Demo</h1>
<p>When using the SuperMap version for development, you only need to specify the path of the SuperMap version through the configuration item <code v-pre>cesiumPath</code> when introducing vue-cesium:</p>
<pre v-pre><code v-pre class="language-js">Vue.use(VueCesium, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/SupermapCesium/Cesium.js'</span>
})

<span class="hljs-comment">// or</span>
<span class="hljs-keyword">import</span> { Viewer } <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-cesium'</span>
Vue.use(Viewer, {
  <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/SupermapCesium/Cesium.js'</span>
})
</code></pre>
<p>Or directly specify the <code v-pre>cesiumPath</code> property on the <code v-pre>vc-viewer</code> component, as in the following example:</p>
<h2>Example</h2>
<h3>Load vc-viewer component with SuperMap</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :animation="animation" :baseLayerPicker="baseLayerPicker" :timeline="timeline" :cesiumPath="cesiumPath" :fullscreenButton="fullscreenButton" :fullscreenElement="fullscreenElement" :infoBox="infoBox" :logo="true" @ready="ready">
        <vc-navigation></vc-navigation>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <vc-layer-imagery ref="layerText">
          <vc-provider-imagery-tianditu mapStyle="cia_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>animation</span>
        <md-switch v-model="animation"></md-switch>
        <span>timeline</span>
        <md-switch v-model="timeline"></md-switch>
        <span>baseLayerPicker</span>
        <md-switch v-model="baseLayerPicker"></md-switch>
        <span>fullscreenButton</span>
        <md-switch v-model="fullscreenButton"></md-switch>
        <span>infoBox</span>
        <md-switch v-model="infoBox"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
