  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">animation</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">timeline</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">baseLayerPicker</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">fullscreenButton</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">infoBox</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">fullscreenElement</span>: <span class="hljs-built_in">document</span>.body,
        <span class="hljs-attr">tk</span>: <span class="hljs-string">'436ce7e50d27eede2f2929307e6b33c0'</span>,
        <span class="hljs-attr">cesiumPath</span>: <span class="hljs-string">'./statics/SuperMapCesium/Cesium.js'</span>
      }
    },
    mounted() {
      <span class="hljs-keyword">this</span>.$refs.vcViewer.createPromise.then(<span class="hljs-function">(<span class="hljs-params">{ Cesium, viewer }</span>) =&gt;</span> {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'viewer is loaded.'</span>)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
