        viewer.scene.globe.enableLighting = <span class="hljs-literal">true</span>
        <span class="hljs-keyword">this</span>.fullscreenElement = <span class="hljs-keyword">this</span>.$refs.viewerContainer
        viewer.entities.add({
          <span class="hljs-attr">id</span>: <span class="hljs-string">'成都欢迎你'</span>,
          <span class="hljs-attr">position</span>: Cesium.Cartesian3.fromDegrees(<span class="hljs-number">104.06</span>, <span class="hljs-number">30.67</span>, <span class="hljs-number">100</span>),
